"use client";

import { useParams, useRouter } from "next/navigation";
import Image from "next/image";
import { getProductBySlug } from "../../../data/products";
import TabNavigator from "@/components/TabNavigator";

export default function ProductDetails() {
  const params = useParams();
  const router = useRouter();
  const product = getProductBySlug(params.slug as string);

  if (!product) {
    return (
      <div className="container mx-auto px-4 py-8">
        <h1 className="text-3xl font-bold text-gray-900">Product Not Found</h1>
      </div>
    );
  }

  const tabs = [
    { label: "Overview", content: <p className="text-gray-600">{product.description}</p> },
    { label: "Applications", content: <ul className="list-disc list-inside space-y-2">{product.applications.map((app, i) => <li key={i}>{app}</li>)}</ul> },
    { label: "Industries", content: <ul className="list-disc list-inside space-y-2">{product.industries.map((ind, i) => <li key={i}>{ind}</li>)}</ul> }
  ];

  return (
    <div className="container mx-auto px-4 py-8 max-w-7xl">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-8">
        {/* Product Images */}
        <div className="space-y-4">
          <Image src={product.images[0]} alt={product.name} width={500} height={500} className="object-cover rounded-lg" />
          <div className="grid grid-cols-3 gap-4">
            {product.images.slice(1).map((image, i) => (
              <Image key={i} src={image} alt={`${product.name} view ${i + 2}`} width={150} height={150} className="object-cover rounded-lg" />
            ))}
          </div>
        </div>

        {/* Product Info */}
        <div>
          <h1 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">{product.name}</h1>
          <TabNavigator tabs={tabs} />
          <button onClick={() => router.push("/enquiry")} className="mt-8 px-8 py-3 bg-[#0E9696] font-bold text-white rounded-lg hover:bg-primary/90 transition">
            Request Quote
          </button>
        </div>
      </div>
    </div>
  );
}
